import React from 'react';
import { actions as AuthActions } from 'reducers/auth';
import FormContainer from 'components/user/form';
import { connect } from 'react-redux';
import { push } from 'react-router-redux';
import { FadeIn } from 'animate-css-styled-components';
// const Identicon = require('identicon.js');

class Signup extends React.Component {
  state = {
    error: false,
    errorMessage: null
  }

  handleSubmit = (formValues) => {
    const { dispatch } = this.props;

    return dispatch(AuthActions.signup(formValues))
    .then(res => {
      this.setState({ error: false, errorMessage: null });
      let { avatar } = res.value;
      // let image = new Identicon('d3b07384d113edec49eaa6238ad5ff00', 50).toString();
      this.setState({
        avatar,
        // image
      });
      // console.log(this.state);
      // dispatch(push('/login'));
      return res;
    })
    .catch(err => {
      // console.log('.catch ', err);
      this.setState({ error: true, errorMessage: err.message });
      return Promise.reject(err);
    })
    
  }

  renderErrorAlert = (message) => {
    return (
      <div className="alert alert-danger" role="alert">
        { message }
      </div>
    )
  }

  render() {
    const { avatar, image } = this.state;

    return (
      <div>
        <FadeIn>
          <div className="jumbotron">
            <h1 className="display-3">Signup Below!</h1>
            <p className="lead">Join the community to access featured content and information.</p>

            {
              avatar ?
                <img width="50" height="50" src={`data:image/png;base64,${avatar}`} />
                  : null
            }
            
            {/* {
              image ?
                <img width="50" height="50" src={`data:image/png;base64,${image}`} />
                : null
            } */}

            { this.state.error ? this.renderErrorAlert(this.state.errorMessage) : null }
            <hr className="my-4" />
            <FadeIn>
              <FormContainer handleSubmit={this.handleSubmit} />
            </FadeIn>
          </div>
        </FadeIn>
        
      </div>
   
    )
  }
}


export default connect(null)(Signup);



